














/*<!-- <style scoped> -->*/
.icon-refresh{
	position: absolute;
	bottom: 20px;
	right: 20px;
}
.icon-refresh:hover{
	-webkit-transition: 1s;
	transition: 1s;
	-moz-transform:rotate(720deg);
	-webkit-transform:rotate(720deg);
	filter:progid:DXImageTransform.Microsoft.BasicImage(rotation=3);
}
.message{
	text-align:center;
	background: url("http://ww4.sinaimg.cn/large/0060lm7Tgw1f8rlzbboigj30zk0k0dgq.jpg");
	background-size:cover;
	color: #fff;
	width: 100%;
	height: 5em;
}
.message h2{
	margin: 0;
	padding-top: 0.3em;
}
.message p{
	font-size: 0.7em;
}
.fresh{
	display: block;
	width: 100%;
	background: #1c2023;
	color: #998;
	text-align: center;
}



